import React, { Component, useRef } from 'react';

import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";


import CircleBtn from "../../components/CircleBtn";
// import OnBoarding from './OnBoarding'

import api from '../../util/api'

import style from './auth.module.css'
import style2 from "../../components/SuccessPage/success.module.css";
import style3 from './onBoarding.module.css'

import arrow from '../../assets/img/chevron-right.svg'

import step1 from '../../assets/img/step1.png'
import step2 from '../../assets/img/step2.png'
import step3 from '../../assets/img/step3.png'
import logo from '../../assets/img/auth/auth_logo.svg'
import auth_back from '../../assets/img/auth/auth_bg.jpg'
import step4 from '../../assets/img/logo-white.svg'
import button from '../../assets/img/circle.png'
import profile from "../../assets/img/recommendation/profile.svg";



const config = [
    {
        title: 'Лучшие места рядом с тобой по твоим интересам',
        imageUrl: step1
    },
    {
        title: 'Выбери тип места!',
        imageUrl: step2
    },
    {
        title: 'Получи персональные рекомендации!',
        imageUrl: step3
    },
    {
        title: 'Узнай что подойдет тебе сегодня!',
        imageUrl: step4,
        background: auth_back
    }
]


class Auth extends Component{

    constructor(props) {
        super(props);

        this.state = {
            pending: false,
            optionsVisible: false,
            landed: false,
            categories: [
                {
                    id: 1,
                    text: 'Try'
                },
            ],
        }
    }

    openLoginWindow = () => {
        const w = 500
        const h = 500

        const left = (window.width/2)-(w/2);
        const top = (window.height/2)-(h/2);

        const newWin = window.open(`about:blank`, "auth", `toolbar=no, location=no, directories=no, status=no, menubar=no, scrollbars=no, resizable=no, copyhistory=no, width=${w}, height=${h}, top=${top}, left=${left}`);

        const interval = setInterval(() => {

           try {

               const location = new URL(newWin.location)

               if (location.searchParams.get('code')) {
                   clearInterval(interval)
                   newWin.close()
                   this.onLogin(location.searchParams.get('code'))
               }
           } catch (e) {
               console.log(e)
           }

       }, 1000)

        newWin.location.href = 'https://www.instagram.com/oauth/authorize?client_id=476859443194257&redirect_uri=https://secure.stayandsmile.com/callback&scope=user_profile&response_type=code'
    }

    openLoginWindow2 = () => {
        const interval = setInterval(() => {
           try {
               clearInterval(interval)
               this.onLogin('test')
           } catch (e) {
               console.log(e)
           }
       }, 1000)
    }

    onLogin = (code) => {
        // var code = 'AQB1pc-2EbP-gMXMEhH_1Hu5r0RgZYEaTuT7Ztinxccx35Xdr62d-8W2fiUhvqCUGvxj9jzcrJnO7FsgPETD6gyavf4_5Fw7pCFaqyNwoT_VMphKR_2FKQkefxaGPYwxajhfVqid9K-UeYby43YkrCAUywGmZ9DxR-QX0VSrVsGw6nWr29eC308g2MVtDdjhXAd9G1UOihfD4LGba6L0Q9R-GVWGFCTmvmTthj9YH-uuTA#_';

        this.setState({
            pending: true
        })

        api.post('login', {
            code
        }).then((res) => {

            this.setState({
                pending: false
            })

            if (res.data.accessToken) {
                this.props.setToken(res.data.accessToken)
                // window.location.reload();
            }
        }).catch(() => {
            this.setState({
                pending: false
            })

        })
    }

    componentDidMount() {
        this.onLogin()
    }

    render() {

        const { pending, categories, optionsVisible,landed } = this.state

        const settings = {
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1,
            arrows: false
        }


        return (
          <Slider {...settings} >
            {config.map((slide, i) => {
                return (
                    <div className={style3.slide} key={i}
                      style={{backgroundImage:slide.background}}>
                         <div className={i < 3 ? style3.image : style3.logo}>
                             <img src={slide.imageUrl} alt=""/>
                         </div>
                        <div className={style3.body}>
                            <div className={style3.title}>
                                {slide.title}
                            </div>
                            <div className={style3.description}>
                                {slide.text}
                            </div>

                            {this.state.optionsVisible && <div className={style2.catMenu}>
                                {categories.map((cat, i) => {

                                    return (
                                        <div key={i} className={style2.catIcon} onClick={() => {
                                            this.setState({
                                                optionsVisible: false
                                            }, () => {
                                                this.openLoginWindow2()
                                            })
                                        }}>
                                            {cat.icon ? <img src={cat.icon} alt=""/> : cat.text}
                                        </div>
                                    )
                                })}
                            </div>}

                            <div className={style3.dots}>
                                {config.map((dot, dotIndex) => {
                                    return (
                                        <button className={`${style3.dot} ${i === dotIndex ? `${style3.dotActive}` : ''}`}
                                            onClick={() => {

                                                if (i === dotIndex) {
                                                    return false
                                                }
                                            }}
                                        />
                                    )
                                })}
                            </div>
                                { i === 3 ?
                                    (<div
                                      style={{    
                                        position: 'absolute',
                                        bottom: '200px'}}>
                                      <CircleBtn onClick={this.openLoginWindow} disabled={pending} style={{
                                          color: 'var(--white)', 
                                          backgroundImage: `url(${button})`, 
                                          backgroundColor: 'transparent', 
                                          borderRadius: '100px' 
                                      }}>
                                        GO
                                      </CircleBtn>

                                      <button style={{padding:'16px 0 0 14px'}} className={`${style2.authBtn}  ${optionsVisible ? `${style2.categoryBtnActive}` : ''} `}
                                        onClick={() => {
                                            this.setState({
                                                optionsVisible: !optionsVisible
                                                })
                                            }}
                                        >
                                        {
                                          'more'
                                        }
                                      </button>
                                    </div>)
                                    : (
                                    <button className={style3['btn-onboard']} 
                                        onClick={() => {}}>
                                        Дальше
                                    </button>
                                    )
                                }
                        </div>
                    </div>
                )
            })}
        </Slider>
        )
    }
}

export default Auth