import React, { Fragment, Component } from 'react';

import Slider from "react-slick";
import Swipe from 'react-easy-swipe';
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import {
  FacebookShareButton,
  InstapaperShareButton,
  TelegramShareButton,
  TwitterShareButton,
  WhatsappShareButton,
} from "react-share";

import {
  FacebookIcon,
  InstapaperIcon,
  TelegramIcon,
  TwitterIcon,
  WhatsappIcon,
} from "react-share";

import gradient from 'random-gradient'

import api from "../../../util/api";
import style from "./newCard.module.css";
import {GoogleMap, Marker, withGoogleMap, withScriptjs} from "react-google-maps";


import arrow from "../../../assets/img/chevron-right.svg";
import faceLike from "../../../assets/img/recommendation/face-like.png";
import faceDislike from "../../../assets/img/recommendation/face-dislike.png";
import happyFace from "../../../assets/img/recommendation/happy-fave.svg";
import sadFace from "../../../assets/img/recommendation/sad-face.svg";
import share from "../../../assets/img/recommendation/share.svg";
import calendar from "../../../assets/img/detailCard/calendar.svg";
import pin from "../../../assets/img/detailCard/pin.svg";
import phone from "../../../assets/img/detailCard/phone.svg";
import chevronDown from "../../../assets/img/detailCard/chevron-bottom.svg";
import chevronUp from "../../../assets/img/detailCard/chevron-up.svg";
import docs from "../../../assets/img/detailCard/docs.svg";
import tags from "../../../assets/img/detailCard/tags.svg";
import verify from "../../../assets/img/verify.svg";
import defaultBg from "../../../assets/img/01.gif";

import PremiumPage from "../../../components/PremiumPage";


const MapWithAMarker = withScriptjs(withGoogleMap((props) =>
    <GoogleMap
        defaultZoom={16}
        defaultCenter={{ lat: parseFloat(props.lat), lng: parseFloat(props.lng) }}
    >
        <Marker
            position={{ lat: parseFloat(props.lat), lng: parseFloat(props.lng) }}
        />
    </GoogleMap>
))


export default class RecomendationCard extends Component {

    

    constructor(props) {
        super(props);

        this.state = {
            rate: false,
            isShown: false,
            step: 1,
            showSharing: false,
            showBooking: false,
            stepLimit: 5,
            photos: this.props.item.posts,
            rateMark: null,
            showPremium: false,
            detailStep: 0
        }
    }

    setRate = (rate = true) => {
        this.setState({
            rate
        })
    }

    toggleHover = () => {
        console.log('toggleHover')
        this.setState({isShown: !this.state.isShown})
    }

    postRate = (mark) => {

        api.post('rate ', {
            locationId: this.props.item.id,
            value: mark
        })

        this.setState({
            rate: false,
            rateMark: mark
        })
    }

    // setShare = (showSharing = true) => {
    //     this.setState({
    //         showSharing
    //     })
    // }

    // setBooking = (showBooking = true) => {
    //     this.setState({
    //         showBooking
    //     })
    // }

    SharePanel = () => {

        const shareUrl = 'https://secure.stayandsmile.com';

        return (
            <div style={{
                padding: '35px',
                background: 'white',
                position: 'absolute',
                zIndex: '9999',
                bottom: '0px',
                left: '0',
                right: '0',
            }}>
                <h4 style={{
                        marginBottom: '50px'
                }}>Поделить интересным местом с другом!</h4>
                <FacebookShareButton url={shareUrl} >
                    <FacebookIcon size={32} round={true} />
                </FacebookShareButton>
                <TelegramShareButton url={shareUrl} >
                    <TelegramIcon size={32} round={true} />
                </TelegramShareButton>
                <InstapaperShareButton url={shareUrl} >
                    <InstapaperIcon size={32} round={true} />
                </InstapaperShareButton>
                <TwitterShareButton url={shareUrl} >
                    <TwitterIcon size={32} round={true} />
                </TwitterShareButton>
                <WhatsappShareButton url={shareUrl} >
                    <WhatsappIcon size={32} round={true} />
                </WhatsappShareButton>
                <button className={style['closed']} onClick={() => {
                    this.setState({
                        showSharing: false
                    })
                }}>
                    ×
                </button>
            </div>
        )
    }

    BookingPanel = () => {


        return (
            <div style={{
                width: '100%',
                padding: '35px',
                background: 'white',
                position: 'absolute',
                zIndex: '9999',
                bottom: '0px',
                height: '70%'
            }}>
                <h4 style={{
                        marginBottom: '120px'
                }}>Book it now</h4>
                <button className={style['closed']} onClick={() => {
                    this.setState({
                        showBooking: false
                    })
                }}>
                    ×
                </button>
            </div>
        )
    }

    renderInfo = (slide) => {
        return slide.type === 'info' && 
            (<Fragment>
            </Fragment>)
    }

    renderMap = (slide) => {
        return slide.type === 'map' &&
            (<div style={{position:'absolute',bottom: '0',width: '98%'}}>
                <div style={{
                    fontSize: '1.25rem',
                    padding: '0 10px'
                }}>
                    Карта :
                </div> 
                <div className={style['detailed-block-main']}>
                    <MapWithAMarker
                        googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyB8weNak3605tIrBrKaNi3Tr1A9AGzVxsE"
                        loadingElement={<div style={{ height: `100%` }} />}
                        containerElement={<div style={{ height: `400px` }} />}
                        mapElement={<div style={{ height: `100%` }} />}
                        lat={this.props.latitude} lng={this.props.longitude}/>
                </div>
            </div>)
    }

    renderDescription = (slide) => {
        return slide.type === 'description' && (slide.text || this.props.description) &&
            (<div style={{position:'absolute',bottom: '0',width: '98%'}}>
                <div className={style['detailed-block-main']}>
                    <div style={{
                        color: '#C432AC'
                    }}>
                        {slide.text || this.props.description}
                    </div>
                </div>
            </div>)
    }

    render() {

        const { rate, showSharing, rateMark, showBooking, showPremium, detailStep, isShown} = this.state

        const settings = {
            dots: true,
            speed: 100,
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: true,
            infinite: false,
            lazyload: true,
            arrows: false,
            centerPadding: 0
        };

        console.log(this.props)

        return (
                <div className={style.wrap}>
                    <div className={style.header}>
                        <button className={style.headerLeft} onClick={this.props.setCurrentRecomendation}>
                            ‹ Назад
                        </button>
                        <div className={style.headerRight}>
                            <button className={`${style.headerBtn} ${style.headerBtnShare}`} onClick={() => {
                                this.setState({
                                    showSharing: !showSharing
                                })
                            }}>
                                <img src={share} alt=""/>
                            </button>
                            <button className={`${style.headerBtn} ${style.headerBtnBook}`} onClick={() => {
                                this.setState({
                                    showPremium: !showPremium
                                })
                            }}>
                                <img src={calendar} alt=""/>
                                <div className={style.headerBtnText}>
                                    бронь
                                </div>
                            </button>
                        </div>
                    </div>
                <div className={style.body}>
                    <Slider {...settings}>
                        <div className={style.slide}>

                            {rateMark === 1 &&
                                <div className={`mark  markPositive`}>
                                    like
                                </div>
                                }

                                {rateMark === 0 &&
                                <div className={` mark markNegative`}>
                                    nope
                                </div>
                            }

                            {rate &&
                                <div className={style.rate}>
                                    <div className={style['rate-title']}>
                                        rate this point
                                    </div>
                                    <div>
                                        <a href="#" className={style['link-white']} onClick={(e) => {
                                            e.preventDefault()
                                            this.setRate(false)
                                        }}>
                                            rate later
                                        </a>
                                    </div>
                                    <div className={style['rate-faces']}>
                                        <button className={style['rate-face-btn']} onClick={() => {
                                            this.setRate(false)
                                        }}>
                                            <img src={happyFace} alt=""/>
                                        </button>
                                        <div className={style['rate-faces-sep']}>
                                            or
                                        </div>
                                        <button className={style['rate-face-btn']} onClick={() => {
                                            this.setRate(false)
                                        }}>
                                            <img src={sadFace} alt=""/>
                                        </button>
                                    </div>

                                    <div style={{
                                        marginTop: 'auto',
                                        marginBottom: '20px',
                                    }}>
                                    </div>
                                </div>
                            }

                            <div className={style['detailed-rating']}
                                style={{bottom: detailStep > 0 ? '350px' :'120px'}}
                            >
                                <div className={style.rating}>
                                    <div>
                                        <button className='button-clear-defaults'
                                                disabled={rateMark || rateMark === 0}
                                                onClick={() => this.postRate(1)}><img src={faceLike} alt=""/></button> +  {this.props.positiveReviews || 'N/A'}
                                    </div>
                                    <div>
                                        <button className='button-clear-defaults'
                                                disabled={rateMark || rateMark === 0}
                                                onClick={() => this.postRate(0)}><img src={faceDislike} alt=""/></button> - {this.props.negativeReviews || 'N/A'}
                                    </div>
                                </div>
                            </div>

                            {
                                <div className={style['detailed-verify']}>
                                    <div className={style.verify}>
                                            <button className='button-clear-defaults'
                                                    onClick={()=>{}}>
                                                <img src={verify} alt="" width={30}/>
                                            </button>
                                    </div>
                                </div>
                            }

                            <div className={style.mainPhoto}>
                                {detailStep > 0 && <button className={style.buttonBack}
                                                           onClick={() => {
                                    this.setState({
                                        detailStep: this.state.detailStep - 1
                                    })
                                }} >
                                    <div>
                                        <img src={chevronUp} alt=""/>
                                    </div>
                                    вернуться
                                </button> }
                                <div className={style.mainPhotoWrap}>
                                    <img src={this.props.item.mainphoto} alt=""/>
                                </div>
                            </div>


                            <div className={style.infoCardWrap}>


                                {detailStep === 0 && <div className={style.infoCard}>
                                    <div className={style.infoCardRow}>
                                        <div className={style.infoCardRowIcon}>
                                            <img src={pin} alt=""/>
                                        </div>
                                         <div className={`${style.infoCardRowText} tc`}>
                                             {this.props.item.name}
                                         </div>
                                     </div>
                                    <button className={style.infoCardMore} onClick={() => {
                                        this.setState({
                                            detailStep: 1
                                        })
                                    }}>
                                        больше
                                        <div>
                                            <img src={chevronDown} alt=""/>
                                        </div>
                                    </button>
                                </div>}

                                {detailStep === 1 &&
                                    <Fragment>
                                        {this.props.item.address ? (<div className={style.infoCard} style={{
                                        marginBottom: '10px'
                                        }}>
                                            <div className={style.map}>
                                                <MapWithAMarker
                                                    googleMapURL="https://maps.googleapis.com/maps/api/js?key=AIzaSyB8weNak3605tIrBrKaNi3Tr1A9AGzVxsE"
                                                    loadingElement={<div style={{ height: `100%` }} />}
                                                    containerElement={<div style={{ height: `185px` }} />}
                                                    mapElement={<div style={{ height: `100%` }} />}
                                                    lat={this.props.latitude} lng={this.props.longitude}/>
                                                    <div className={`${style.infoCardGreyText} tc`}>
                                                        {this.props.item.address.replace('Россия, Москва,','')}
                                                    </div>
                                            </div>
                                        </div>) : (
                                            <a href= {this.props.website} target="_blank"
                                               style={{
                                                   color: '#7935CB',
                                               }}
                                               rel="noopener nofollow">
                                                {this.props.item.website}
                                            </a>
                                        )}
                                        <div className={style.infoCard}>
                                            <div className={style.infoCardRow}>
                                                <div className={style.infoCardRowIcon}>
                                                    <img src={phone} alt=""/>
                                                </div>
                                                <div className={`${style.infoCardRowText} tc`}>
                                                    {this.props.item.phone}
                                                </div>
                                            </div>
                                            <button className={style.infoCardMore} onClick={() => {
                                                this.setState({
                                                    detailStep: 2
                                                })
                                            }}>
                                                больше
                                                <div>
                                                    <img src={chevronDown} alt=""/>
                                                </div>
                                            </button>
                                        </div>
                                    </Fragment>
                                }

                                {detailStep === 2 && <Fragment>
                                    {this.props.description && <div className={style.infoCard} style={{
                                        marginBottom: '10px'
                                    }}>
                                        <div className={style.infoCardRow}>
                                            <div className={style.infoCardRowIcon}>
                                                <img src={docs} alt=""/>
                                            </div>
                                            <div className={`${style.infoCardRowText}`}>
                                                Описание:
                                                <div className={style.infoCardRowDescription}>
                                                    {this.props.item.description}
                                                </div>
                                            </div>
                                        </div>
                                    </div>}
                                    <div className={style.infoCard} style={{
                                        marginBottom: '10px'
                                    }}>
                                        <div className={style.infoCardRow}>
                                            <div className={style.infoCardRowIcon}>
                                                <img src={docs} alt=""/>
                                            </div>
                                            <div className={`${style.infoCardRowText}`}>
                                                Теги:
                                                <div className={style.infoCardRowDescription}>
                                                    {this.props.tags.map((tag) => <span key={tag}>#{tag} </span>)}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className={style.label}>
                                        детали
                                    </div>
                                    <div className={style.infoCard}>
                                        <div className={style.infoCardDetails}>
                                            <div className={`${style.infoCardDetailsItem} ${style.infoCardDetailsItemYellow}`}>
                                                Средний чек
                                                <div>
                                                    {this.props.averageBillUSD > 0 ? `средний чек : ${this.props.averageBillUSD}₽` : 'FREE'}
                                                </div>
                                            </div>
                                            <div className={style.infoCardDetailsItem}>
                                                Время работы
                                                <div>
                                                    {this.props.item.workhours}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </Fragment>}
                            </div>

                        </div>
                        <div className={style.slide}>
                            <div className={style.galleryHeading}>
                                Чтобы увидеть что публикуют другие пользователи
                            </div>
                            <div className={style.photoGrid}>
                                {this.props.item.posts.map((post, i)=> {

                                    if (i > 5 || !post) {
                                        return null
                                    }

                                    return (
                                        <div>
                                           {isShown && (
                                            <div style={{color:'white',fontSize:'8px'}}>
                                              {post.text}
                                            </div>
                                          )}
                                            <div className={style.gridPhotoItem} key={post.photo} onClick={() => {
                                                    this.setState({
                                                        showPremium: true
                                                    })
                                                }}
                                            >
                                                <img 
                                                    onMouseEnter={this.toggleHover}
                                                    onMouseLeave={this.toggleHover}
                                                    src={'https://avatars.mds.yandex.net/get-pdb/2115127/0406d341-f336-4064-8194-b0f675f624fc/s1200'} alt=""/>
                                            </div>
                                        </div>
                                    )
                                })}
                            </div>
                            <button className={style.buyBtn} onClick={() => {
                                this.setState({
                                    showPremium: true
                                })
                            }}>
                                купить
                            </button>

                        </div>
                    </Slider>
                </div>
                {
                    showSharing && this.SharePanel()
                }

                {showPremium &&

                <div style={{position: 'absolute', top: 0}}>
                    <PremiumPage showPremium={() => {
                        this.setState({
                            showPremium: !this.state.showPremium
                        })
                    }} />
                </div> }
            </div>
        )
    }
}